"use client";

import { useEffect, useRef, useState } from "react";
import { motion } from "framer-motion";
import {
  PROMPT_LINE1,
  PROMPT_LINE2_PREFIX,
  PROMPT_WORDS,
} from "@/lib/page5-text";

const LINE2_DELAY_MS = 3000;
const WORD_INTERVAL_MS = 2000;
const HOLD_AFTER_WORDS_MS = 2000;

interface ValentinePromptProps {
  onComplete: () => void;
}

/**
 * Scene 3: "So will you do me the honor of being" → 3s → "my Valentine" →
 * then append "now", "and", "forever", "after?" one word at a time (2s intervals, smooth pop-in). Then onComplete.
 */
export function ValentinePrompt({ onComplete }: ValentinePromptProps) {
  const [showLine1, setShowLine1] = useState(true);
  const [showLine2Prefix, setShowLine2Prefix] = useState(false);
  const [extraWords, setExtraWords] = useState<string[]>([]);
  const onCompleteRef = useRef(onComplete);
  onCompleteRef.current = onComplete;

  useEffect(() => {
    const t2 = setTimeout(() => setShowLine2Prefix(true), LINE2_DELAY_MS);
    return () => clearTimeout(t2);
  }, []);

  useEffect(() => {
    if (!showLine2Prefix) return;
    let index = 0;
    const timeouts: ReturnType<typeof setTimeout>[] = [];
    const addWord = () => {
      if (index >= PROMPT_WORDS.length) {
        timeouts.push(setTimeout(() => onCompleteRef.current(), HOLD_AFTER_WORDS_MS));
        return;
      }
      setExtraWords((prev) => [...prev, PROMPT_WORDS[index]]);
      index += 1;
      timeouts.push(setTimeout(addWord, WORD_INTERVAL_MS));
    };
    timeouts.push(setTimeout(addWord, WORD_INTERVAL_MS));
    return () => timeouts.forEach(clearTimeout);
  }, [showLine2Prefix]);

  return (
    <motion.div
      className="max-w-2xl mx-auto px-6 py-6 text-center space-y-4"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.4 }}
    >
      {showLine1 && (
        <motion.p
          className="font-display text-2xl sm:text-3xl md:text-4xl text-white drop-shadow-lg"
          initial={{ opacity: 0, y: 8 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.4 }}
        >
          {PROMPT_LINE1}
        </motion.p>
      )}
      {showLine2Prefix && (
        <motion.p
          className="font-display text-2xl sm:text-3xl md:text-4xl text-white drop-shadow-lg"
          initial={{ opacity: 0, y: 8 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.4 }}
        >
          {PROMPT_LINE2_PREFIX}
          {extraWords.map((word, i) => (
            <motion.span
              key={i}
              className="inline-block ml-2"
              initial={{ opacity: 0, scale: 0.7, y: 12 }}
              animate={{ opacity: 1, scale: 1, y: 0 }}
              transition={{ type: "spring", stiffness: 260, damping: 22 }}
            >
              {" "}
              {word}
            </motion.span>
          ))}
        </motion.p>
      )}
    </motion.div>
  );
}
